import React from 'react'

import { VehicleItemListProps } from './Props'
import {
	Box,
	Image,
	Title,
	Subtitle,
	Price,
	SpaceBetween,
	Year,
	KM,
	ContentImage,
} from './styles'

export const VehicleItemList: React.FC<VehicleItemListProps> = ({
	vehicle,
	...rest
}) => {
	return (
		<Box {...rest}>
			<ContentImage>
				<Image src={vehicle.Image} />
			</ContentImage>
			<Title>
				{vehicle.Make} {vehicle.Model}
			</Title>
			<Subtitle>{vehicle.Version}</Subtitle>
			<Price>
				{new Intl.NumberFormat('pt-BR', {
					style: 'currency',
					currency: 'BRL',
					maximumSignificantDigits: 7,
				}).format(parseFloat(vehicle.Price))}
			</Price>
			<SpaceBetween>
				<Year>
					{vehicle.YearFab}/{vehicle.YearModel}
				</Year>
				<KM>{vehicle.KM} km</KM>
			</SpaceBetween>
		</Box>
	)
}
