import { action } from 'typesafe-actions'
import { VehiclesTypes, Vehicle } from './types'

export const loadRequest = (pageID: number) => {
	return action(VehiclesTypes.LOAD_REQUEST, { pageID })
}
export const loadSuccess = (data: Vehicle[]) =>
	action(VehiclesTypes.LOAD_SUCCESS, { data })
export const loadError = () => action(VehiclesTypes.LOAD_FAILURE)
