import React, { Component } from 'react'
import { Dispatch, bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import {
	Container,
	BoxContentForm,
	CheckboxGroup,
	AdvancedSearchIcon,
	AdvancedSearch,
	ActionsForm,
	ActionsFormButtons,
	ClearFilters,
	ActionButton,
	SelectGroup,
	FormGridRow,
	FormGridRowReverse,
} from './styles'
import Checkbox from '../Checkbox'
import {
	SelectMake,
	SelectModel,
	SelectRadius,
	SelectLocation,
	SelectYear,
	SelectPriceRange,
	SelectVersion,
} from '../Select'

import { Make } from '../../store/ducks/makes/types'
import { Model } from '../../store/ducks/models/types'
import { Version } from '../../store/ducks/versions/types'
import { Vehicle } from '../../store/ducks/vehicles/types'
import * as MakeActions from '../../store/ducks/makes/actions'
import * as ModelActions from '../../store/ducks/models/actions'
import * as VersionActions from '../../store/ducks/versions/actions'
import * as VehiclesActions from '../../store/ducks/vehicles/actions'
import { ApplicationState } from '../../store'

interface StateProps {
	makes: Make[]
	models: Model[]
	versions: Version[]
	vehicles: Vehicle[]
}

interface DispacthProps {
	loadRequestMakes(): void
	loadRequestModels(MakeID: number): void
	loadRequestVersions(ModelID: number): void
	loadRequestVehicles(pageID: number): void
}

type Props = StateProps & DispacthProps

class SearchForm extends Component<Props> {
	state = {
		model: false,
		version: false,
	}

	componentDidMount() {
		const {
			loadRequestMakes,
			loadRequestModels,
			loadRequestVersions,
			loadRequestVehicles,
		} = this.props
		loadRequestMakes()
		loadRequestModels(0)
		loadRequestVersions(1)
		loadRequestVehicles(1)
	}

	handleMake = (id: string) => {
		const { loadRequestModels } = this.props
		const makeID = parseInt(id, 10)
		loadRequestModels(makeID)
		if (makeID !== 0)
			this.setState({
				model: true,
			})
		else
			this.setState({
				model: false,
			})
	}

	handleModel = (id: string) => {
		const { loadRequestVersions } = this.props
		const modelID = parseInt(id, 10)
		loadRequestVersions(modelID)
		if (modelID !== 0)
			this.setState({
				version: true,
			})
		else
			this.setState({
				version: false,
			})
	}

	render() {
		const { makes, models, versions } = this.props
		return (
			<Container>
				<BoxContentForm>
					<CheckboxGroup>
						<Checkbox label="Novos" />
						<Checkbox label="Usados" />
					</CheckboxGroup>
					<FormGridRow>
						<SelectGroup>
							<SelectLocation
								label="Onde"
								options={[
									{ Name: 'Cidade', ID: '' },
									{ Name: 'São Paulo', ID: 'sao_paulo' },
									{
										Name: 'Rio de Janeiro',
										ID: 'rio_de_janeiro',
									},
								]}
							/>
							<SelectRadius
								label="Raio"
								options={[
									{ Name: '10km', ID: 10 },
									{ Name: '25km', ID: 25 },
									{ Name: '50km', ID: 50 },
									{ Name: '100km', ID: 100 },
									{ Name: '200km', ID: 200 },
								]}
							/>
						</SelectGroup>
						<SelectMake
							label="Marca"
							options={makes}
							onChange={(e) => this.handleMake(e.target.value)}
						/>
						<SelectModel
							label="Modelo"
							options={models}
							onChange={(e) => this.handleModel(e.target.value)}
							disabled={!this.state.model}
						/>
					</FormGridRow>
					<FormGridRowReverse>
						<SelectYear
							label="Ano Desejado"
							options={[
								{ Name: 'Ano Desejado', ID: 0 },
								{ Name: '2008', ID: 2008 },
								{ Name: '2009', ID: 2009 },
								{ Name: '2020', ID: 2020 },
							]}
						/>
						<SelectPriceRange
							label="Faixa de Preço"
							options={[
								{ Name: 'Faixa de Preço', ID: 0 },
								{ Name: '2008', ID: 2008 },
								{ Name: '2009', ID: 2009 },
								{ Name: '2020', ID: 2020 },
							]}
						/>
						<SelectVersion
							label="Versões"
							options={versions}
							disabled={!this.state.version}
						/>
					</FormGridRowReverse>
					<ActionsForm>
						<AdvancedSearch>
							<AdvancedSearchIcon />
							Busca Avançada
						</AdvancedSearch>
						<ActionsFormButtons>
							<ClearFilters>Limpar filtros</ClearFilters>
							<ActionButton size="large">
								Ver Ofertas
							</ActionButton>
						</ActionsFormButtons>
					</ActionsForm>
				</BoxContentForm>
			</Container>
		)
	}
}
const mapStateToProps = (state: ApplicationState) => ({
	makes: state.makes.data,
	models: state.models.data,
	versions: state.versions.data,
	vehicles: state.vehicles.data,
})

const mapDispatchToProps = (dispatch: Dispatch) => ({
	...bindActionCreators(
		{
			loadRequestMakes: MakeActions.loadRequest,
			loadRequestModels: ModelActions.loadRequest,
			loadRequestVersions: VersionActions.loadRequest,
			loadRequestVehicles: VehiclesActions.loadRequest,
		},
		dispatch
	),
})

export default connect(mapStateToProps, mapDispatchToProps)(SearchForm)
