import React from 'react'
import { VehiclesListProps } from './Props'

import { VehiclesList } from './styles'
import { VehicleItemList } from '../VehicleItemList'

const VehiclesListComponent: React.FC<VehiclesListProps> = ({
	vehicles,
	...rest
}) => {
	return (
		<VehiclesList {...rest}>
			{vehicles.map((vehicle) => (
				<VehicleItemList vehicle={vehicle} key={vehicle.ID} />
			))}
		</VehiclesList>
	)
}

export default VehiclesListComponent
