import { call, put } from 'redux-saga/effects'

import api from '../../../services/api'
import { loadSuccess, loadError } from './actions'

export function* load() {
	try {
		const response = yield call(api.get, 'OnlineChallenge/Make')

		const all = { ID: 0, Name: 'Marca: Todas' }
		yield put(loadSuccess([all, ...response.data]))
	} catch (error) {
		yield put(loadError())
	}
}
