import { Reducer } from 'redux'
import { ModelsState, ModelsTypes } from './types'

const INITIAL_STATE: ModelsState = {
	data: [],
	error: false,
	loading: false,
}

const reducer: Reducer<ModelsState> = (state = INITIAL_STATE, action) => {
	switch (action.type) {
		case ModelsTypes.LOAD_REQUEST:
			return { ...state, loading: true }
		case ModelsTypes.LOAD_SUCCESS:
			return {
				...state,
				loading: false,
				error: false,
				data: action.payload.data,
			}
		case ModelsTypes.LOAD_FAILURE:
			return { ...state, loading: false, error: true, data: [] }
		default:
			return state
	}
}

export default reducer
