import { action } from 'typesafe-actions'
import { VersionsTypes, Version } from './types'

export const loadRequest = (ModelID: number) => {
	return action(VersionsTypes.LOAD_REQUEST, { ModelID })
}
export const loadSuccess = (data: Version[]) =>
	action(VersionsTypes.LOAD_SUCCESS, { data })
export const loadError = () => action(VersionsTypes.LOAD_FAILURE)
