import { all, takeLatest } from 'redux-saga/effects'

import { MakesTypes } from './makes/types'
import { ModelsTypes } from './models/types'
import { VersionsTypes } from './versions/types'
import { VehiclesTypes } from './vehicles/types'
import { load as loadMakes } from './makes/sagas'
import { load as loadModels } from './models/sagas'
import { load as loadVersions } from './versions/sagas'
import { load as loadVehicles } from './vehicles/sagas'

export default function* rootSaga() {
	return yield all([
		takeLatest(MakesTypes.LOAD_REQUEST, loadMakes),
		takeLatest(ModelsTypes.LOAD_REQUEST, loadModels),
		takeLatest(VersionsTypes.LOAD_REQUEST, loadVersions),
		takeLatest(VehiclesTypes.LOAD_REQUEST, loadVehicles),
	])
}
