import React from 'react'
import { Container, Checkbox, Checkmark } from './styles'

import { CheckboxProps } from './Props'

const CheckboxComponent: React.FC<CheckboxProps> = ({ label, ...rest }) => {
	return (
		<Container>
			<Checkbox type="checkbox" {...rest} />
			<Checkmark />
			{label}
		</Container>
	)
}

export default CheckboxComponent
