import React from 'react'

import { SelectProps, LocationProps } from './Props'
import { SelectField } from './styles'
import { Make } from '../../store/ducks/makes/types'
import { Model } from '../../store/ducks/models/types'

export const SelectMake: React.FC<SelectProps<Make>> = ({
	options,
	label,
	...rest
}) => {
	return (
		<SelectField label={label} {...rest}>
			{options.map(({ ID, Name }: Make) => (
				<option value={ID} key={ID}>
					{Name}
				</option>
			))}
		</SelectField>
	)
}

