import { action } from 'typesafe-actions'
import { MakesTypes, Make } from './types'

export const loadRequest = () => action(MakesTypes.LOAD_REQUEST)
export const loadSuccess = (data: Make[]) =>
	action(MakesTypes.LOAD_SUCCESS, { data })
export const loadError = () => action(MakesTypes.LOAD_FAILURE)
