import React, { useState, FormEvent, useEffect } from 'react'
import { Dispatch, bindActionCreators } from 'redux'
import { connect } from 'react-redux'

import * as VehiclesActions from '../../store/ducks/vehicles/actions'
import { Vehicle } from '../../store/ducks/vehicles/types'
import { ApplicationState } from '../../store'

import SearchForm from '../../components/SearchForm'
import VehiclesList from '../../components/VehiclesList'

import { Container, PaginationButtons, ActionButtonPaginate } from './styles'

interface StateProps {
	vehicles: Vehicle[]
	loading: Boolean
}

interface DispatchProps {
	loadRequestVehicles(pageID: number): void
}

type Props = StateProps & DispatchProps

const Home: React.FC<Props> = ({ vehicles, loading, loadRequestVehicles }) => {
	const [page, setPage] = useState(1)
	const [buttonPrevDisable, setButtonPrevDisable] = useState(true)
	const [buttonNextDisable, setButtonNextDisable] = useState(false)

	useEffect(() => {
		setButtonPrevDisable(!(page > 1))
		setButtonNextDisable(vehicles.length < 10)
		loadRequestVehicles(page)
	}, [loadRequestVehicles, vehicles.length, page])

	function handleNextPage(e: FormEvent) {
		e.preventDefault()
		if (!loading && !(vehicles.length < 10)) setPage(page + 1)
	}

	function handlePrevPage(e: FormEvent) {
		e.preventDefault()
		if (!loading && page > 1) setPage(page - 1)
	}
	return (
		<Container>
			<SearchForm />
			<VehiclesList vehicles={vehicles} />
			<PaginationButtons>
				<ActionButtonPaginate
					disabled={buttonPrevDisable}
					onClick={handlePrevPage}
				>
					Anterior
				</ActionButtonPaginate>
				<ActionButtonPaginate
					disabled={buttonNextDisable}
					onClick={handleNextPage}
				>
					Próximo
				</ActionButtonPaginate>
			</PaginationButtons>
		</Container>
	)
}

const mapStateToProps = (state: ApplicationState) => ({
	vehicles: state.vehicles.data,
	loading: state.vehicles.loading,
})

const mapDispatchToProps = (dispatch: Dispatch) => ({
	...bindActionCreators(
		{
			loadRequestVehicles: VehiclesActions.loadRequest,
		},
		dispatch
	),
})

export default connect(mapStateToProps, mapDispatchToProps)(Home)
