import styled from 'styled-components'
import { darken } from 'polished'
import { ActionButtonProps } from './Props'

export const ActionButton = styled.button<ActionButtonProps>`
	padding: 0.6rem 2rem;
	font-size: 0.9rem;
	font-weight: 600;
	border-radius: 3px;
	background: none;
	border: ${(props) =>
		props.primary ? 'solid 1px orange' : 'solid 1px #a21f1f'};
	color: ${(props) => (props.primary ? 'orange' : '#a21f1f')};
	transition: 50ms linear all;
	cursor: pointer;

	&:hover {
		color: ${(props) =>
			props.primary ? darken(0.1, 'orange') : darken(0.1, '#a21f1f')};
		border: ${(props) =>
			props.primary
				? `solid 1px ${darken(0.1, 'orange')}`
				: `solid 1px ${darken(0.1, '#a21f1f')}`};
	}

	@media screen and (max-width: 780px) {
		width: 100%;
		margin-bottom: 1rem;
	}
`
