/**
 * Action types
 */
export enum VersionsTypes {
	LOAD_REQUEST = '@versions/LOAD_REQUEST',
	LOAD_SUCCESS = '@versions/LOAD_SUCCESS',
	LOAD_FAILURE = '@versions/LOAD_FAILURE',
}

/**
 * Data types
 */

export interface Version {
	ModelID: number
	ID: number
	Name: string
}

/**
 * State type
 */
export interface VersionsState {
	readonly data: Version[]
	readonly loading: boolean
	readonly error: boolean
}
