import { Reducer } from 'redux'
import { MakesState, MakesTypes } from './types'

const INITIAL_STATE: MakesState = {
	data: [{ ID: 0, Name: 'Todos' }],
	error: false,
	loading: false,
}

const reducer: Reducer<MakesState> = (state = INITIAL_STATE, action) => {
	switch (action.type) {
		case MakesTypes.LOAD_REQUEST:
			return { ...state, loading: true }
		case MakesTypes.LOAD_SUCCESS:
			return {
				...state,
				loading: false,
				error: false,
				data: action.payload.data,
			}
		case MakesTypes.LOAD_FAILURE:
			return { ...state, loading: false, error: true, data: [] }
		default:
			return state
	}
}

export default reducer
