import { Reducer } from 'redux'
import { VersionsState, VersionsTypes } from './types'

const INITIAL_STATE: VersionsState = {
	data: [],
	error: false,
	loading: false,
}

const reducer: Reducer<VersionsState> = (state = INITIAL_STATE, action) => {
	switch (action.type) {
		case VersionsTypes.LOAD_REQUEST:
			return { ...state, loading: true }
		case VersionsTypes.LOAD_SUCCESS:
			return {
				...state,
				loading: false,
				error: false,
				data: action.payload.data,
			}
		case VersionsTypes.LOAD_FAILURE:
			return { ...state, loading: false, error: true, data: [] }
		default:
			return state
	}
}

export default reducer
