import { call, put } from 'redux-saga/effects'

import api from '../../../services/api'
import { loadSuccess, loadError } from './actions'
import { AnyAction } from 'redux'

export function* load({ payload }: AnyAction) {
	try {
		const { MakeID } = payload
		const response = yield call(api.get, 'OnlineChallenge/Model', {
			params: {
				MakeID,
			},
		})

		const all = { MakeID: 0, ID: 0, Name: 'Modelo: Todos' }
		yield put(loadSuccess([all, ...response.data]))
	} catch (error) {
		yield put(loadError())
	}
}
