import { action } from 'typesafe-actions'
import { ModelsTypes, Model } from './types'

export const loadRequest = (MakeID: number) => {
	return action(ModelsTypes.LOAD_REQUEST, { MakeID })
}
export const loadSuccess = (data: Model[]) =>
	action(ModelsTypes.LOAD_SUCCESS, { data })
export const loadError = () => action(ModelsTypes.LOAD_FAILURE)
