/**
 * Action types
 */
export enum VehiclesTypes {
	LOAD_REQUEST = '@vehicles/LOAD_REQUEST',
	LOAD_SUCCESS = '@vehicles/LOAD_SUCCESS',
	LOAD_FAILURE = '@vehicles/LOAD_FAILURE',
}

/**
 * Data types
 */

export interface Vehicle {
	ID: number
	Make: string
	Model: string
	Version: string
	Image: string
	KM: number
	Price: string
	YearModel: number
	YearFab: number
	Color: string
}

/**
 * State type
 */
export interface VehiclesState {
	readonly data: Vehicle[]
	readonly loading: boolean
	readonly error: boolean
}
